.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-header nav a {
  color: white;
  text-decoration: none; /* Optional: removes the underline from links */
  margin: 0 10px;       /* Optional: adds some spacing between links */
}

.App-header nav a:hover {
  text-decoration: underline; /* Optional: adds underline on hover for better UX */
}

.About, .Portfolio-highlights {
  padding: 20px;
}

.Highlight-item {
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px;
  display: inline-block;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

/*---------------------Art Component---------------------*/
.art-preview {
  cursor: pointer;
  border: 1px solid #ddd;
  margin: 10px;
  padding: 10px;
}

.art-preview img {
  max-width: 100px; /* Adjust as needed */
  height: auto;
}

.art-full-view img {
  max-width: 300px; /* Adjust as needed */
  margin: 10px;
}

.set-preview-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.set-preview {
  cursor: pointer;
  border: 1px solid #ddd;
  margin: 10px;
  padding: 10px;
  text-align: center;
}

.set-preview img {
  max-width: 150px; /* Adjust as needed */
  height: auto;
  margin-bottom: 10px;
}

.image-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.image-gallery img {
  max-width: 200px; /* Adjust as needed */
  margin: 10px;
}
